import React from 'react'

function Color() {
  return (
        <>
        <div style={{display:"flex" ,flexWrap:"wrap" ,marginLeft:"150px"}}>
        <span className='Color'role="note" style={{backgroundColor:"black"}}></span>
        <span className='Color'role="note" style={{backgroundColor:"green"}}></span>
        <span className='Color'role="note" style={{ background:" rgb(253, 186, 94)"}}></span>
        <span className='Color'role="note" style={{ background:"rgb(100, 8, 211)"}}></span>
        <span className='Color'role="note" style={{ background:"rgb(255, 255, 255)"}}></span>
        <span className='Color'role="note" style={{ background:"rgb(249, 0, 154)"}}></span>
        <span className='Color'role="note" style={{ background:"rgb(244, 92, 67)"}}></span>
        <span className='Color'role="note" style={{ background:"rgb(31, 57, 255)"}}></span>
        <span className='Color'role="note" style={{ background:"rgb(235, 115, 39)"}}></span>
      
        </div>
     </> 
  )
}

export default Color;