import React from "react";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { FiFacebook,FiTwitter } from "react-icons/fi";
import { BsInstagram } from "react-icons/bs";
import logo from "../../images/logo.jpeg"
function Footer() {
  return (
    <>
    <hr className="hr"/>
    <Container>
      <Row style={{marginLeft:"0px"}}>
         <Col sm Col-sm={12}>
               <ul type={"none"}>
                  <strong>About us</strong>  
                   <li>Our Story</li>
                   <li>Careers</li>
               </ul>
        </Col>
        <Col sm>
          <ul type={"none"}  sm={12}>
                 <strong>Account</strong>
            <li>Sign in</li>
            <li>Register</li>
            <li>Order Status</li>
          </ul>
        </Col>
        <Col sm>
          <ul type={"none"} Col-sm={12}>
                <strong>Help</strong>
            <li>Contact Us</li>
            <li>Order Status</li>
            <li>Returns</li>
          </ul>
        </Col>
        <Col sm  Col-sm={12}>
          <ul type={"none"}>
             <strong> Follow Us!</strong>
            <li>Lorem ipsum dolor sit </li>
            <li>consectetur adipiscing elit</li>
            <li>sed do eiusmod</li>
            <li><h5><BsInstagram/>  <FiFacebook/>    <FiTwitter/></h5> </li>
          </ul>
        </Col>
            </Row>
        </Container>
           <hr className="hr"/>
           <Container>
           <Row style={{marginLeft:"0px"}}>
           <Col sm  Col-sm={12}>
               <ul type={"none"}>
               <img src={logo} style={{width:"100px" ,height:"80px" ,marginTop:"-30px"}}/>
             </ul>
        </Col>
         <Col sm  Col-sm={12}>
               <ul type={"none"}>
                Company Name Address Ave, City Name, State ZIP
             </ul>
        </Col>
        <Col sm  Col-sm={12}>
             <a href="" className="ancar">Terms of Use</a>  <a href="" style={{marginLeft:"20px"}}  className="ancar"> Privacy Policy</a>
                   </Col>
        </Row>
        </Container>
          </>
  );
}

export default Footer;


