import axios from 'axios'
import React,{useState,useEffect} from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Color, Hr,CheckBox1 ,Icon,Select} from "../atoms";


function ProductService() {
    const [data, setdata] = useState([])
    const [products, setproduct] = useState([])

    useEffect(() => {
     axios.get(`https://fakestoreapi.com/products`).then((res)=>{
        setdata(res.data .slice(0,5))
        setproduct(res.data)
        console.log(res.data)
     })
    }, [])
   
    const options =[
      {
        value:"select"
      },
      {
        value:"sort by Price Hight to Low"
      },
      {
        value:"sort by Price Low to Hight"
      },
      {
        value:"women's clothing"
      },
      {
        value:"men's clothing"
      },
      {
        value:"jewelery"
      },
      {
        value:"electronics"
      },
    ]
    const sortonchange=(e)=>{
      let result=[]
      let text =e.currentTarget.value;
      if(text=="sort by Price Hight to Low"){
        result=products.sort((a,b)=>b.price - a.price);
        let result2=[...result];
        setdata(result2)
      }else if(text=="sort by Price Low to Hight"){
        result=products.sort((a,b)=>a.price - b.price);
        let result2=[...result];
        setdata(result2)
      }else if(text=="select"){
        result=products.slice(0,5);
        setdata(result)
      }
      else{
        result=products.filter((e=>e.category === text));
        setdata([...result]);
      }
    }
    
  return (
<>
<Container>
     
      <Row style={{marginTop:"10px"}}>
        <Col sm>Clothing/ Womens/ Outerweas</Col>
        <Col sm>5 Results</Col>
        <Col sm>       </Col>
        <Col sm>
     
     <Select onChange={sortonchange } title={"select"} options={options}/>
        </Col>
        </Row>
        </Container>
        
<Row style={{marginTop:"20px"}}>
    <Col md={3} sm={12}>
<div>
    <div>
   <h6> Filters</h6>
   <Hr/>
    </div>
    <div style={{textAlign:"match-parent"}}>
  <h6>Size</h6>
  <CheckBox1 value={"X-Small"} /> 
  <CheckBox1 value={"Medium"} /> 
  <CheckBox1 value={"Large"} /> 
  <CheckBox1 value={"X-Large"} /> 
 <p> <a href='#' style={{color:"black" ,marginLeft:"25px"}}>Show more</a></p>
    </div>
    <Hr/>
    <div>
        <h6>Style</h6>
    <CheckBox1 value={"Outdoor"} /> 
  <CheckBox1 value={"Casual"} /> 
  <CheckBox1 value={"Active"} /> 
  <CheckBox1 value={"Running"} /> 
  <CheckBox1 value={"Outfits"} /> 
  <CheckBox1 value={"Working"} /> 
    </div>
    <Hr/>
    <div>
    <h6>Color</h6>
    <Color/>
    </div>
    <Hr/>
    <div>
    <h6>Brand</h6>
<CheckBox1 value={"Calvin Klein"} /> 
  <CheckBox1 value={"Dolce & Gottaboo"} /> 
  <CheckBox1 value={"Miu Miu"} /> 
  <CheckBox1 value={"Prada"} /> 
  <CheckBox1 value={"Rag & Bone"} /> 
  <CheckBox1 value={"Gucci"} />
  <CheckBox1 value={"Marni"} />
  <CheckBox1 value={"Dior"} />
  <CheckBox1 value={"Salvatore"} />
    </div>
    <Hr/>

</div>

    </Col>
    <Col md={9} sm={12}>
    <div style={{display:"flex",flexWrap:"wrap", marginLeft:"100px"}}>
{
    data.map((e)=>{
        return(<>
        <div style={{width:"18rem" , margin:"10px"}}>
        <img src={e.image} className="image2" />
        <h5 >{e.title.slice(0,20)}</h5>
        <p >${e.price}</p>
        {/* <h6>{e.category}</h6> */}
        <Icon/>
        </div>
            </>
        )
    })
}
</div>
    </Col>
</Row>


</>
  )
}

export default ProductService