import React from 'react'

function Select(props) {
    let title=props.title?props.title:"Select"
    
  return (
    <>
    <select title={title} onChange={props.onChange?props.onChange:null}>
        
    {
        props.options.map((e)=>{
            return <option>{e.value}</option>
        })
    }
    </select>
    
    </>
  )
}

export default Select