import React from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { BsSearch ,BsPerson ,BsBagCheck} from "react-icons/bs";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import logo from "../../images/logo.jpeg"
function Header() {
  return (
<>
<Container>
   <Navbar bg="light" expand="lg" style={{position: "sticky",top:"0px",
    zIndex: "100px"}}>      
        <Navbar.Brand href="#home"><img src={logo} style={{width:"100px" ,height:"80px"}}/></Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto" >
            <Nav.Link href="#home">Women</Nav.Link>
            <Nav.Link href="#link">Men</Nav.Link>
            <Nav.Link href="#link">Smart Gare</Nav.Link>
            <Nav.Link href="#link">Accessories</Nav.Link>
            </Nav>
          <Nav className="headicons">
            <Nav.Link href="#home"><p><BsSearch/>  search</p>  </Nav.Link>
            <Nav.Link href=""> <p><BsPerson/> sign in </p>    </Nav.Link>
            <Nav.Link href="#link"><p><BsBagCheck/></p></Nav.Link>
           </Nav>
           </Navbar.Collapse>
       </Navbar>
       <Row>
       <Col sm ="4" >
       <span className='box' ><u>Womens</u></span>
       </Col>
       <Col sm="8">
       <img src='https://neetavagare.github.io/static/media/Banner.6031014ce9e78b297505.png' className='image1' />
       </Col>
       </Row>
  
  {/* <Row>
    <Col sm="4"></Col>
  </Row> */}
      </Container>
</>
  )
}



export default Header