import React from 'react'

function CheckBox1(props) {
  let className = props.className?props.className:"checkbox"

  return (
    <>
    <div style={{textAlign:"justify" ,marginLeft:"170px"}}>

    <input  type={"checkbox"} style={{color: "#91959c", height: "21px", width: "21px" ,marginLeft:"0px"}} className={className} name={"checkbox"}/>
    <label className={props.value} style={{fontSize: "18px",marginLeft: "11px", marginTop: "4px",lineHeight:"10px" ,position: "absolute"}}>{props.value}</label>
    </div>
    </>
  )
}

export default CheckBox1;